html,
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-items: center;
  background: #000000;
  color: lightblue;
  text-align: center;
}

#control-panel {
  width: 100%;
}

canvas {
  display: block;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
